.NavMain {
	padding-left: 4px;

	&-Link {
		max-width: 129px;

		color: #000;
		white-space: nowrap;
		text-decoration: none;
	}

	&-Tab {
		width: calc(14.28% - 21px);
		margin: 1px 1px 1px 0;
		padding: 5px 10px 5px 10px;

		float: left;

		font-size: 11px;
		text-align: center;
		color: #000;

		background: #ffe2c6;
		background: linear-gradient(to bottom, #fff0e6, #ffd2b6);
		border-radius: 14px 3px 14px 3px;
		box-shadow: inset 0 0 2px rgba(77, 77, 77, 0.15);

		cursor: pointer;

		&:hover {
			background: #ffc788;
			background: linear-gradient(to top, #fff0e6, #ffd2b6);
			box-shadow: inset 0 1px 2px rgba(77, 77, 77, 0.15);
		}
	}
}
