@import '../../../../breakpoints';

.Header {
	max-width: 1000px;
	margin: 0 auto;

	&-Logo {
		float: left;
		margin: 5px;
		width: 50px;
		height: 50px;

		@include media-breakpoint-up(sm) {
			& {
				margin: 5px 33px 5px 33px;
				width: 100px;
				height: 100px;
			}
		}
	}

	&-Title {
		margin: 5px;
		padding-top: 5px;

		font-size: 16px;
		line-height: 16px;
		letter-spacing: 2.5px;

		@include media-breakpoint-up(sm) {
			& {
				margin: 0 0 10px;

				font-size: 30px;
				line-height: 30px;
			}
		}
	}

	&-Link {
		text-decoration: none;
		color: #000;

		border-bottom: 0.05rem solid rgba(0, 0, 0, 0.25);

		&:hover {
			color: #f60;

			border-bottom-color: rgba(255, 102, 51, 0.25);
		}

		&_selected {
			color: #000 !important;

			border-bottom: none;
		}
	}

	&-SubjectImg {
		display: none;

		@include media-breakpoint-up(mil) {
			& {
				display: block;
			}
		}
	}

	&-WebsiteDesc {
		display: none;

		@include media-breakpoint-up(sm) {
			& {
				display: block;

				font-size: 14px;
				line-height: 20px;
			}
		}
	}

	&-SubjectTitle {
		font-size: 14px;
		line-height: 16px;

		@include media-breakpoint-up(sm) {
			& {
				margin-top: 15px;

				font-size: 20px;
				line-height: 20px;
			}
		}
	}
}
