@import '../../breakpoints';

html {
	font-size: 18px;
}

@media (min-width: 768px) {
	html {
		font-size: 14px;
	}
}

.PageLayout {
	position: relative;

	max-width: 1000px;
	height: auto !important;
	margin: auto;
	padding-left: constant(safe-area-inset-left);
	padding-left: env(safe-area-inset-left);
}

.PageLayout-Nav {
	margin-top: 0.4rem;
}

.PageLayout-Nav_desktop {
	display: none;
}

.PageLayout-Nav_mobile {
	display: block;
	cursor: pointer;
	text-align: center;
	font-size: 16px;
	background: linear-gradient(#fff0e6, #ffd2b6);
	clear: both;
}

@media (min-width: 768px) {
	.PageLayout-Nav_mobile {
		display: none;
	}

	.PageLayout-Nav_desktop {
		display: block;
	}
}

.PageLayout-Grid {
	display: grid;
	grid-template-columns: 1fr;
	grid-gap: 0.5rem;
	margin: 0.4rem 0 1.5rem;

	@include media-breakpoint-up(md) {
		& {
			grid-template-columns: 175px 1fr;
		}
	}

	@include media-breakpoint-up(mil) {
		& {
			grid-template-columns: 170px 1fr;
		}
	}
}

.PageLayout-Sidebar {
	display: none;

	@include media-breakpoint-up(md) {
		& {
			display: block;
		}
	}
}

.PageLayout-Content {
	margin: 0 0.5rem;

	@include media-breakpoint-up(md) {
		& {
			margin-left: 0;
		}
	}

	@include media-breakpoint-up(mil) {
		& {
			margin-right: 0;
		}
	}
}

.PageLayout-Main {
	margin-bottom: 1rem;
}

